export default class Notepad {
  constructor(notes = []) {
    this._notes = notes;
    this.saveLocal();
  }
  get notes() {
    return this._notes;
  }

  saveLocal() {
    try {
      const currentNotes = JSON.parse(localStorage.getItem('notes'));
      if(currentNotes == null){
        localStorage.setItem('notes', JSON.stringify(this._notes));
      } else {
      localStorage.setItem('notes', JSON.stringify(currentNotes));
      }
    } catch (error) {
      console.log(error);
    }
  }

  findNoteById = id => {
    new Promise((resolve) => {
      const foundNote = this._notes.find(elem => elem.id === id);
      resolve(foundNote);
    });
  }

  saveNote = note => {
    new Promise((resolve) => {
      this._notes.push(note);
      resolve(note);
    });
  }

  deleteNote = id => {
    new Promise((resolve) => {
      this.findNoteById(id);
      const currentNotes = JSON.parse(localStorage.getItem('notes'));
      const newNotes = currentNotes.filter(e => e.id !== id);
      console.log(newNotes);
      localStorage.setItem('notes', JSON.stringify(newNotes));
    });
  }

  updateNoteContent = (id, newContent) => {
    new Promise((resolve) => {
      const currentNote = this.findNoteById(id);
      if (currentNote) {
        Object.assign(currentNote, newContent);
      }
      resolve(currentNote);
    });
  }

  updateNotePriority = (id, newPriority) => {
    new Promise((resolve) => {
      const currentNote = this.findNoteById(id);
      currentNote.priority = newPriority;
      resolve(currentNote);
    });
  }

  filterNotesByQuery = query => {
    new Promise((resolve) => {
      const filteredNotes = this._notes.filter(
        e =>
          e.title.toLowerCase().includes(query.toLowerCase()) ||
          e.body.toLowerCase().includes(query.toLowerCase())
      );
      resolve(filteredNotes);
    });
  }

  filterNotesByPriority = priority => {
    new Promise((resolve) => {
      let newArray = [];
      let currentPriority = this._notes.find(elem => elem.priority === priority);
      newArray.push(currentPriority);
      resolve(newArray);
    });
  }
}
